




















































import { Component, Prop, Vue } from 'vue-property-decorator'
import { createOrUpdateMenu, getEditMenuInfo } from '@/api/menu'

@Component
export default class CreateOrEdit extends Vue {
  @Prop()
  private isEdit: any

  private form: any = {
    parentId: -1, // -1 表示没有上级菜单
    name: '',
    href: '',
    icon: '',
    orderNum: 0,
    description: '',
    shown: false
  }

  private parentMenuList: any[] = []

  private created () {
    this.loadMenuInfo()
  }

  private async loadMenuInfo () {
    const { data } = await getEditMenuInfo(this.$route.params.id || -1)
    if (data.data.menuInfo) {
      this.form = data.data.menuInfo
    }
    if (data.code === '000000') {
      this.parentMenuList = data.data.parentMenuList
    }
  }

  private async onSubmit () {
    const { data } = await createOrUpdateMenu(this.form)
    if (data.code === '000000') {
      this.$message.success('提交成功')
      this.$router.back()
    }
  }
}
